export var Sesong;

(function (Sesong) {
  Sesong[Sesong["FjellSommer"] = 0] = "FjellSommer";
  Sesong[Sesong["BySommer"] = 1] = "BySommer";
  Sesong[Sesong["Vinter"] = 2] = "Vinter";
})(Sesong || (Sesong = {}));

export function getSesongLabel(s) {
  var label = '';

  switch (s) {
    case Sesong.FjellSommer:
      label = 'Sommer på fjellet';
      break;

    case Sesong.BySommer:
      label = 'Sommer i byen';
      break;

    default:
      label = Sesong[s];
  }

  return label;
}