import { Aktivitet } from '../../models/aktivitet';
import { stringArrayToItems } from './utils';
import { Kategori } from '../../models/kategori';
export function getSkiTing(valg) {
  var skiTing = [];
  var langrenn = valg.aktiviteter.includes(Aktivitet.Langrenn);
  var fjellski = valg.aktiviteter.includes(Aktivitet.Fjellski);
  var randone = valg.aktiviteter.includes(Aktivitet.Randonée);
  var skøyteski = valg.aktiviteter.includes(Aktivitet.Skøyteski);
  var skalPåSki = langrenn || fjellski || randone || skøyteski;

  if (skalPåSki) {
    skiTing.push('Ski', 'Staver', 'Skisko');

    if (valg.spesielleBehov) {
      skiTing.push('Varme såler');
    }
  }

  if (langrenn || fjellski) {
    skiTing.push('Smøring');
  }

  if (fjellski) {
    skiTing.push('Kortfeller');
    skiTing.push('Langfeller');
  }

  if (randone) {
    skiTing.push('Feller');
    skiTing.push('Skarejern', 'Skibriller', 'Hjelm');
  }

  return stringArrayToItems(skiTing, Kategori.Ski);
}