import { getToalettSaker } from './getToalettSaker';
import { getIsbreTing } from './getIsbreTing';
import { getKlaer } from './getKlaer';
import { getDivTing } from './getDivTing';
import { getOvernattingTing } from './getOvernattingTing';
import { getSkredTing } from './getSkredTing';
import { getSkiTing } from './getSkiTing';
import { getKlatreutstyr } from './getKlatreutstyr';
import { getFjellturTing } from './getFjellturTing';
import { getSpesielleTing } from './getSpesielleTing';
import { getKajakkTing } from '../../pakk/listMakers/getKajakkTing';
export var alleLister = [getToalettSaker, getIsbreTing, getKlaer, getDivTing, getOvernattingTing, getSkredTing, getSkiTing, getKlatreutstyr, getFjellturTing, getSpesielleTing, getKajakkTing];

var sortAlphabetically = function sortAlphabetically(item1, item2) {
  return item1.navn > item2.navn ? 1 : -1;
};

function removeDuplicates(items) {
  var uniqueItems = [];
  items.forEach(function (candidate) {
    var duplicate = uniqueItems.find(function (it) {
      return it.navn === candidate.navn && it.kategori === candidate.kategori;
    });

    if (!duplicate) {
      uniqueItems.push(candidate);
    } else {
      duplicate.antall = duplicate.antall > candidate.antall ? duplicate.antall : candidate.antall;
    }
  });
  return uniqueItems;
}

export function getAlleTing(valg) {
  return removeDuplicates(alleLister.flatMap(function (rule) {
    return rule(valg);
  }).sort(sortAlphabetically));
}