/* Tar et array og putter ting i grupper basert på gruppekategorier */
export function groupArray(array, getGroupCategoryForElement) {
  return array.reduce(function (acc, arrayEntry) {
    var currentCategory = getGroupCategoryForElement(arrayEntry);
    var categoryIndex = acc.findIndex(function (group) {
      return group.category === currentCategory;
    });

    if (categoryIndex >= 0) {
      acc[categoryIndex].array.push(arrayEntry);
    } else {
      acc.push({
        category: currentCategory,
        array: [arrayEntry]
      });
    }

    return acc;
  }, []);
}