import { getEnumAsArray } from '../utils/enum';
export var Aktivitet;

(function (Aktivitet) {
  Aktivitet[Aktivitet["Klatring"] = 0] = "Klatring";
  Aktivitet[Aktivitet["Fottur"] = 1] = "Fottur";
  Aktivitet[Aktivitet["Tinderangling"] = 2] = "Tinderangling";
  Aktivitet[Aktivitet["Jogging"] = 3] = "Jogging";
  Aktivitet[Aktivitet["Randon\xE9e"] = 4] = "Randon\xE9e";
  Aktivitet[Aktivitet["Isbre"] = 5] = "Isbre";
  Aktivitet[Aktivitet["Fjellski"] = 6] = "Fjellski";
  Aktivitet[Aktivitet["Langrenn"] = 7] = "Langrenn";
  Aktivitet[Aktivitet["Sk\xF8yteski"] = 8] = "Sk\xF8yteski";
  Aktivitet[Aktivitet["Bytur"] = 9] = "Bytur";
  Aktivitet[Aktivitet["Fotografi"] = 10] = "Fotografi";
  Aktivitet[Aktivitet["Kajakk"] = 11] = "Kajakk";
})(Aktivitet || (Aktivitet = {}));

export function getAktivitetAsArray() {
  return getEnumAsArray(Aktivitet);
}
export function erSommerAktivitet(aktivitet) {
  return [Aktivitet.Klatring, Aktivitet.Fottur, Aktivitet.Tinderangling, Aktivitet.Jogging, Aktivitet.Kajakk].includes(aktivitet);
}
export function erVinterAktivitet(aktivitet) {
  return [Aktivitet.Randonée, Aktivitet.Fjellski, Aktivitet.Langrenn, Aktivitet.Skøyteski].includes(aktivitet);
}
export function skalGåPåTur(aktiviteter) {
  return [Aktivitet.Randonée, Aktivitet.Fjellski, Aktivitet.Langrenn, Aktivitet.Skøyteski, Aktivitet.Fottur, Aktivitet.Tinderangling, Aktivitet.Klatring].some(function (it) {
    return aktiviteter.includes(it);
  });
}
export function erAnnenAktivitet(aktivitet) {
  return [Aktivitet.Bytur].includes(aktivitet);
}