import { Sesong } from '../../models/sesong';
import { Aktivitet } from '../../models/aktivitet';
export function overnatting(valg) {
  return valg.overnatting.length > 0;
}
export function sikkerhetVinter(valg) {
  return valg.sesong === Sesong.Vinter || [Aktivitet.Randonée, Aktivitet.Fjellski].some(function (it) {
    return valg.aktiviteter.includes(it);
  });
}
export function stringArrayToItems(array, kategori, antall) {
  return array.map(function (ting) {
    return {
      navn: ting,
      antall: antall || 1,
      kategori: kategori
    };
  });
}
export function objektMedAntallTilItems(object, kategori) {
  return Object.entries(object).map(function (ting) {
    return {
      navn: ting[0],
      antall: ting[1],
      kategori: kategori
    };
  });
}